
.skill{
    
    color:rgb(154,159,170);
        display: flex;
    justify-content:center;
    margin-top: 20px;
    font-weight: normal;
    font-family: 'Courier New', Courier, monospace;
    flex-wrap: wrap;



}
.singleSkill{
    border: 1px solid black;
    border-radius: 20px;
    box-shadow:1px 1px 1px #282c34;
    margin: 10px;
}
.singleSkill:hover{
    font-size: 25px;
    transition: ease 1s;
    box-shadow: 1px 0.5px 3px rgba(233,233,235);
    /*display: block;*/
    filter:drop-shadow(0 0 5px #ffffff);



}
.image{
    width: 70%;
    height: 140px;
}
.titleSkill{
    font-size: 15px;
    margin-bottom: 10px;
box-shadow: none;
}




@media (max-width: 500px) {
 

    .skill{
        display: flex;
        justify-content:center;
        font-weight: normal;
        font-family: 'Courier New', Courier, monospace;
        flex-wrap: wrap;
        width:auto;
        height: auto;
    }
    .singleSkill{
        width: 30%;
        border: 1px solid black;
        border-radius: 20px;
        box-shadow:1px 1px 1px #282c34;
        margin: 14px;
    }
    .image{
        width: 50%;
        height: auto;
        margin-top: 5px;
    
    }
    .titleSkill{
        font-size: 14px;
    }
  
    .menu{
        display: none;
    }
    .title {
        align-items: center;
        text-align: center;
    }
    .subtitle{
        align-items: center;
        text-align: center;
    }
    .icon{
        padding: 3px;
        margin: 0;
        justify-content: center;
        align-items: center;
      
    }
.text-about{
    line-height: 1.5    ;
    width: 100%;
    height: auto;
    font-size: 18px;
    text-align: center;
    margin-bottom: 20px;    
}




}